.portfolioboard
    overflow: hidden

.localization-button-container
    position: absolute
    bottom: 20px
    left: 20px
    z-index: 100

.localization-button
    border: 0
    background: none
    text-decoration: underline
    transition: opacity 0.5s
    cursor: pointer
    font-size: 24px
    opacity: 0.5

.localization-button:hover
    opacity: 1

.portfolioboard-object
    z-index: 2
    position: absolute
    cursor: initial
    opacity: 0.9
    transition: opacity 1.2s

.portfolioboard-object:hover
    opacity: 1

.portfolioboard-map
    position: fixed
    opacity: 0.7
    transition: opacity 0.5s
    background: linear-gradient(90deg, #a175ffa6 0%, #ff878791 100%)
    border: solid #0000004d 1px
    border-radius: 6px
    padding-right: 2px
    padding-bottom: 2px
    z-index: 150

@media (max-width: 670px)
    .portfolioboard-map
        display: none

.portfolioboard-map:hover
    opacity: 1

.portfolioboard-map-window
    z-index: 3
    cursor: pointer
    position: absolute
    background-color: rgb(255 255 255 / 12%)
    border-radius: 6px
    transition: transform 0.2s
    overflow: hidden

.portfolioboard-map-object
    position: absolute
    transform-origin: top left
