.portfolioboard-1
    .skills-card
        background-color: #DCD6A4
        display: flex
        flex-direction: column
        justify-content: space-between

    .skills-card-container
        margin-left: 48px
        margin-right: 12px

    .skills-card-title
        border-bottom: 5px solid #324C5F

    .skills-card-title h1
        position: relative
        margin-bottom: 12px
        left: -42px

    .skills-card-list
        h3
            margin-top: 12px
        ul
            display: flex
            flex-direction: column
            flex-wrap: wrap
        ul li
            color: #324C5F
        p
            margin: 0
            margin-top:1em
            color: #324C5F

    .skills-card
        .card-navigation 
            a
                color:#324C5F
            a svg path
                fill: #324C5F

    .slided-card
        margin-top: 12px
        position: relative
        left:-72px
        background-color: #324C5F
        padding: 6px 0 0 6px
        h3
            color: #FBFBFB
            margin: 0
        ul li
            color: #FBFBFB
            margin-top: 6px

.portfolioboard-2
    .skills-card
        background: linear-gradient(180deg, rgba(220, 214, 164, 0.132) 0%, rgba(220, 214, 164, 0.066) 100%)
        box-shadow: 0px 2px 2px 0px #00000033 inset, 0px -2px 2px 0px #00000033 inset,0px 0px 50px 0px #FFFFFF26 inset
        backdrop-filter: blur(8px)
        display: flex
        flex-direction: column
        justify-content: space-between
        height: 590px
        width: 488px
        
    .skills-card-container
        margin-left: 48px
        margin-right: 12px

    .skills-card-title h1
        color: #FFF9F9
        position: relative
        margin: 0
        margin-top: 15px

    .skills-card-list
        h3
            margin-top: 12px
            color:#DCD6A4
        ul
            display: flex
            flex-direction: column
            flex-wrap: wrap
        ul li
            color: #FFFFFF
        p
            margin: 0
            margin-top:1em
            color: #FFFFFF

    .skills-card
        .card-navigation 
            a
                color:#DCD6A4
            a svg path
                fill: #DCD6A4

    .slided-card
        margin-top: 12px
        position: relative
        h3
            color: #DCD6A4
            margin: 0
        ul li
            color: #FBFBFB
            margin-top: 6px

@media (max-width: 425px)
    .portfolioboard-2
        .skills-card
            width: 350px 
            height: 690px
