.portfolioboard-1
    .contacts-card
        font-family: Avantgarde, TeX Gyre Adventor, URW Gothic L, sans-serif

    .contacts-card h1
        font-family: Koulen ,Avantgarde
        color: white
        font-size: 24px
        font-weight: normal
        text-align: center
        letter-spacing: 4px
        margin: 0
        user-select: none

    .contact-group
        margin-top: 60px
        display: flex
        flex-direction: row
        padding: 0 30px

    .contact-item
        flex: 1
        display: flex
        align-items: center

    .contact-item img
        display: inline
        user-select: none
        width: 33px
        height: 19px
        user-select: none

    .contact-item
        font-size: 12px
        color: #EBEBEB
        text-decoration: none

    .contact-messengers-group
        margin-top: 60px
        display: flex
        flex-direction: row
        justify-content: space-between
        align-items: center
        padding: 0 115px

    .contact-messenger img
        width: 48.66px
        height: 40.35px
        user-select: none

.portfolioboard-2
    .contacts-card
        font-family: Avantgarde, TeX Gyre Adventor, URW Gothic L, sans-serif
        height: 206px
        width: 406px

    .contacts-card h1
        font-family: Koulen ,Avantgarde
        color: #DCD6A4
        font-size: 65px
        font-weight: normal
        text-align: center
        letter-spacing: 4px
        margin: 0
        user-select: none

    .contact-group
        // margin-top: 60px
        display: flex
        flex-direction: row
        // padding: 0 30px

    .contact-item
        flex: 1
        display: flex
        align-items: center

    .contact-item img
        display: inline
        user-select: none
        width: 33px
        height: 19px
        user-select: none

    .contact-item
        font-size: 14px
        color: #EBEBEB
        text-decoration: none

    .contact-messengers-group
        margin-top: 35px
        display: flex
        flex-direction: row
        justify-content: space-between
        align-items: center
        padding: 0 115px

    .contact-messenger img
