        user-select: none

@media (max-width: 425px)
    .portfolioboard-2
        .contacts-card
            width: 350px 
            height: 340px

        .contact-group
            flex-direction: column
            align-items: center
            gap: 30px
        
        .contact-messengers-group
            padding: 0 70px
