$backgroundColor: #FBFBFB
$backgroundColorBlock: #DCD6A4
$backgroundShadowColorBlock: #936a6a

.portfolioboard-1
    .about-card
        background-color: $backgroundColor
        font-size: 16px
        display: flex
        flex-direction: column

    .about-card-entering
        height: 315px
        display: flex
        flex-direction: row

    .about-card-entering img
        width: 281px
        height: 315px
        user-select: none

    .about-card-entering-block
        display: flex
        flex-direction: column
        margin-bottom: 24px
        justify-content: space-between

    .about-card-entering-text
        padding: 5px 10px 0 10px

    .about-card-jobs
        padding: 12px 
        margin-left: 12px
        background-color: $backgroundColorBlock
        box-shadow: 0px 0px 4px 0px $backgroundShadowColorBlock

    .about-card-bottom-container
        margin-top: 24px
        display: flex
        flex-direction: column
        justify-content: space-between
        height: 100%

    .about-card-education
        padding: 0 12px

.portfolioboard-2
    .about-card
        box-shadow: 0px 2px 2px 0px #00000033 inset, 0px -2px 2px 0px #00000033 inset, 0px 0px 50px 0px #FFFFFF26 inset
        background: linear-gradient(180deg, rgba(220, 214, 164, 0.132) 0%, rgba(220, 214, 164, 0.066) 100%)
        font-size: 16px
        backdrop-filter: blur(8px)
        display: flex
        flex-direction: column
        width: 527px
        height: 565px

    .about-card-entering
        // height: 315px
        display: flex
        flex-direction: row

    .about-card-entering img
        padding: 2px
        width: 281px
        height: 315px
        user-select: none

    .about-card-entering-block
        display: flex
        flex-direction: column
        margin-bottom: 24px
        justify-content: space-between

    .about-card-entering-text
        padding: 5px 10px 0 10px

    .about-card-jobs
        padding: 12px 
        margin-left: 12px
        margin-right: 2px
        background: linear-gradient(111.66deg, #DCD6A4 0%, rgba(255, 223, 174, 0.33) 100%)

    .about-card-bottom-container
        margin-top: 24px
        display: flex
        flex-direction: column
        justify-content: space-between
        height: 100%

    .about-card-education
        padding: 0 12px

@media screen and (max-width: 500px)
    .portfolioboard-2
        .about-card
            width:350px 
            height: 760px
        .about-card-entering
            flex-direction: column
            align-items: center
            justify-content: start
            img
                padding-top: 10px
                width: 196px
                height: 220px
            .about-card-entering-block
                justify-content: start
                margin: 0
               

    